
import React, { Component } from 'react';
import moment from 'moment'

import tomato from './img/tomato-icon.png'

const mode = {
    'Work': { name: "Work", minutes: 25 },
    'Break': { name: "Break", minutes: 5 },
    'LongBreak': { name: "Longer Break", minutes: 15 }
}

class Clock extends Component {
    constructor(props) {
        super(props);
        this.state = {
            started: false,
            timerEnd: null,
            timerDisplay: "25:00",
            workCounter: 1,
            mode: mode.Work
        };
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(),
            1000
        );
        Notification.requestPermission();
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    timeDisplay(seconds) {
        let sec = (seconds % 60)
        let result = Math.floor(seconds / 60) + ':' + (sec > 9 ? sec : '0' + sec);
        return result;
    }

    spawnNotification(title, body) {
        var options = {
            body: body,
            icon: tomato
        }
        var n = new Notification(title, options);
    }

    tick() {
        if (this.state.started) {
            // start timer
            let t = this.state.timerEnd
            let inSec = t.diff(moment(), 'seconds')
            //let sec = (inSec % 60)
            //let result = Math.floor(inSec / 60) + ':' + (sec > 9 ? sec : '0'+sec);


            if (inSec <= 0) {
                // timer is finished, change modes
                switch (this.state.mode) {
                    case mode.Work:
                        this.setState((state, props) => ({
                            started: false,
                            mode: state.workCounter === 4 ? mode.LongBreak : mode.Break,
                            timerDisplay: "00:00"
                        }))
                        break;
                    case mode.Break:
                        this.setState((state, props) => ({
                            started: false,
                            mode: mode.Work,
                            timerDisplay: "00:00",
                            workCounter: state.workCounter + 1
                        }))
                        break;
                    case mode.LongBreak:
                        this.setState((state, props) => ({
                            started: false,
                            mode: mode.Work,
                            timerDisplay: "00:00",
                            workCounter: 1
                        }))
                        break;
                    default:
                        break;
                }
                this.spawnNotification("Pomodoro", "Timer is over!");
            }
            else {
                this.setState({
                    timerDisplay: this.timeDisplay(inSec)
                })
            }
        }
        else {
            this.setState({
                timerDisplay: this.timeDisplay(this.state.mode.minutes * 60)
            })
        }
    }

    handleClick(e) {
        if (e.target.name === 'btnStart') {
            let timer = this.state.mode.minutes;
            this.setState({
                started: true,
                timerEnd: moment().add(timer, 'minutes')
            })
        }
        if (e.target.name === 'btnStop') {
            this.setState({
                started: false,
                timerDisplay: "25:00"
            })
        }

    }

    render() {
        return (
            <div className="App-box">
                <img id="tomato" src={tomato} alt="logo" />
                <h1>{this.state.timerDisplay}</h1>
                <h3>{this.state.mode.name}</h3>
                {
                    this.state.started
                        ? (<button className="button-stop" name="btnStop" onClick={this.handleClick} >STOP</button>)
                        : (<button className="button-start" name="btnStart" onClick={this.handleClick}>START</button>)
                }
            </div>
        )
    }
}

export default Clock;